<template>
    <el-row>
    <el-col :span="24">
      <el-form ref="articleCreate" :model="article" :rules="createRules">
        <el-row style="margin-top:20px">
          <el-col :span='10' :push="1">
            <el-form-item label=""  label-width="90px" prop="title">
              <el-input style="width:260px" v-model="article.title" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="10" :push="3" >
            <el-form-item label="" label-width="90px" prop="classify">
              <el-select v-model="article.classify" placeholder="">
                <el-option v-for="(item,index) in classifyList" :label="item.classify" :key="index" :value="item.classify"></el-option>
            </el-select>
            </el-form-item>
          </el-col>
      </el-row>
        <el-row style="margin-top:20px">
          <el-col :span='12'>
            <!--  -->
            <el-form-item class="show" prop="content" >
               <el-input type="textarea" v-model="article.content" :rows="25" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <!--  -->
            <div style="background:#fff;margin:0 0 0 20px;height:526px;overflow-y:auto;" v-html="markedToHtml"></div>
          </el-col>
        </el-row>
        <el-form-item style="padding:20px 20px 0 0 " >
          <el-button type="primary" style="float:right;" @click="editArticle('articleCreate')" :loading="load" size='small'>{{btnText}}</el-button>
            <el-button style="float:right;margin-right:10px"@click="cancel" size='small' :loading="load"></el-button>
      </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import api from '../../api/index'
import marked from 'marked'
import hlj from 'highlight.js'
import  'highlight.js/styles/atom-one-dark.css'
export default {
    data(){
        return {
            article:{
                id:'',
                title:'',
                content :'',
                classify:'',
            },
            classifyList:[],
            btnText:'',
            load:false,
            createRules:{
                title:[
                     { required: true, message: '', trigger: 'blur' }
                ],
                content:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                classify:[
                    { required: true, message: '', trigger: 'change' }
                ]
            }, 
        }
        
    },
    methods:{
        editArticle(formName){
            this.$refs[formName].validate((valid)=>{
                if(valid){
                    this.$confirm('','',{})
                        .then(()=>{
                            this.load=true;
                            this.btnText=''
                            api.editArticle({
                                contentToMark:this.markedToHtml,
                                ...this.article
                            })
                            .then((result)=>{
                                this.btnText=''
                                this.load=false
                                if(result.data.code==200){
                                    this.$notify({
                                        title:'',
                                        message:result.data.message,
                                        type:'success'
                                    })
                                }else{
                                    this.$notify({
                                        title:'',
                                        message:result.data.message,
                                        type:'error'
                                    })
                                }
                            })
                            setTimeout(()=>{
                              this.$router.push({path:'/admin/article/articleList'})
                            },500)
                        })
                }
            })
        },
        cancel(){
            this.$router.push({path:'/admin/article'})
        },
        initial(){
            api.getOneArticle({id:this.$route.params.postId})
                .then((result)=>{
                    this.article.id = result.data.articleInfo._id
                    this.article.title = result.data.articleInfo.title
                    this.article.classify = result.data.articleInfo.classify
                    this.article.content = result.data.articleInfo.content
                })
        }
    },
    mounted(){
        api.getClassify()
            .then((result)=>{
                if(result.data.code===200){
                    this.classifyList=result.data.lists
                }
            })
        this.initial()
    },
    computed:{
        markedToHtml(){
            marked.setOptions({
        highlight: function (code) {
        return hlj.highlightAuto(code).value;
        }
      });
      return marked(this.article.content);
    }
    }
}
</script>

<style>

</style>



